
import React from "react";
import { useHistory } from "react-router-dom";
import { useSelector } from "react-redux";
import Axios from "axios";
import { Button } from "antd";


const PrevSceneTeleport = ({ gameId,setScene }) => {
    const history = useHistory();
    const user = useSelector((state) => state.user);

    const playingListPop = () => {

        if (user) {
            //! auth      .
            Axios.get("/api/users/playing-list/pop").
                then(response => {
                    if (response.data.success) {
                        setScene({})
                        history.push({
                            pathname: `/gameplay`,
                            state: {
                                sceneId: response.data.teleportSceneId,
                                gameId: gameId,
                            }
                        })
                    }
                    else {
                        alert(" .")
                    }
                })
        }
        else {
            alert("   .")
        }

    }


    return (
        <Button
                onClick={playingListPop}
                className="ending_button"
        > 
        </Button>

    )

}


export default PrevSceneTeleport;
