
import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { useSelector } from "react-redux";
import Axios from "axios";
import { Button, Modal } from "antd";

import "../SceneEndingPage/SceneEndingPage.css";



const FirstSceneTeleport = ({ gameId, setScene }) => {
    const history = useHistory();
    const user = useSelector((state) => state.user);
    const [isWarningVisible, setIsWarningVisible] = useState(false)

    const playingListClear = () => {

        if (user) {
            //! auth      .
            Axios.get("/api/users/playing-list/clear").
                then(response => {
                    if (response.data.success) {
                        setIsWarningVisible(false)
                        setScene({})
                        history.push({
                            pathname: `/gameplay`,
                            state: {
                                sceneId: response.data.teleportSceneId,
                                gameId: gameId,
                            }
                        })
                    }
                    else {
                        setIsWarningVisible(false)

                        alert(" .")
                    }
                })
        }


    }
    const cancelModal = () => {
        setIsWarningVisible(false)
    }
    const openModal = () => {
        setIsWarningVisible(true)
    }
    return (
        <React.Fragment>
            <Button
                onClick={openModal}
                className="ending_button"
            > 
            </Button>
            <Modal
                title="  "
                visible={isWarningVisible}
                maskClosable={false}
                closable={false}
                centered = {true}
                footer={
                    <React.Fragment>
                        <Button key = "ok" type= "primary" onClick={playingListClear}>
                            
                        </Button>
                        <Button key = "cancel" onClick={cancelModal}>
                            
                        </Button>
                    </React.Fragment>
                }
            >

                <p>  .</p>
                <p>   .</p>
            </Modal>
        </React.Fragment>
    )


}


export default FirstSceneTeleport;
